import * as React from "react";
import TagList from "../tagList";

export default function ProductBrandSection({ brand, imgSrc, children }) {
  return (
    <div className="col-12 p-4">
      <p className="fs-6 mt-4">{brand.label}</p>
      <p className="fs-2 fw-bold">{brand.title}</p>
      <p className="fs-3">{brand.desc}</p>
      <img alt="Viva" src={imgSrc} width={"100%"} className="" />
    </div>
  );
}
