import * as React from "react";

const pillStyles = {
  pointerEvents: "none",
};

export default function TagList({ tags }) {
  return (
    <>
      {tags.map((tag, idx) => {
        return (
          <span key="idx">
            <span
              className="btn btn-outline-dark btn-sm rounded-pill"
              style={pillStyles}
            >
              {tag}
            </span>
            &nbsp;&nbsp;
          </span>
        );
      })}
    </>
  );
}
