import * as React from "react";
import ProductDesc from "./productDesc";
import ProductTable from "./productTable";
import ProductImgSection from "./productImgSection";
import HeroAnimation from "../home/heroAnimation";

export default function ProductHero({ elem, mediaSrc, isImg, children }) {
  return (
    <>
      {isImg ? (
        <ProductImgSection imgSrc={mediaSrc} />
      ) : (
        <HeroAnimation>
          <div className="">
            <video autoPlay loop muted>
              <source src={mediaSrc} type="video/mp4" />
            </video>
          </div>
        </HeroAnimation>
      )}
      <div className="row fw-bold align-items-center">
        <div className={"col-6 p-4 text-center"}>
          <ProductDesc elem={elem} />
        </div>
        <div className="col-6 p-4">
          <ProductTable elem={elem} />
        </div>
      </div>
    </>
  );
}
