import * as React from "react";
import TagList from "../tagList";

export default function ProductTable({ elem, children }) {
  return (
    <table className="table fs-5">
      <thead>
        <tr>
          <th scope="col">OUR ROLE</th>
          <th scope="col">YEAR</th>
        </tr>
      </thead>
      <tbody className="table-group-divider">
        <tr className={"border-0"}>
          <td>
            {elem.services.map((service, idx) => (
              <p key={idx}>{service}</p>
            ))}
          </td>
          <td>{elem.year}</td>
        </tr>
      </tbody>
    </table>
  );
}
